import { useRef, useEffect, useState } from 'react';
import React from 'react';
import { motion, useAnimationControls, useInView } from 'framer-motion';

function MobileNav() {
  const ref = useRef(null);
  const humMenu = useRef(null);
  const isIinView = useInView(ref);
  const navControl = useAnimationControls();
  const [isOpen, setIsOpen] = useState(false);

  const handleToggle = () => {
    setIsOpen((isOpen) => !isOpen);
  };

  const variants = {
    open: { opacity: 1, x: 0 },
    closed: { opacity: 0, x: '-150%' },
  };

  useEffect(() => {
    if (!isIinView) {
      setIsOpen(false);
      humMenu.current.checked = false;
    }
  }, [isIinView]);

  return (
    <nav>
      <label className="hamburger-menu">
        <input
          ref={humMenu}
          type="checkbox"
          onClick={(e) => {
            handleToggle();

            console.log(humMenu.current.checked);
          }}
        />
      </label>
      <motion.div>
        <motion.ul
          ref={ref}
          className="nav-menu"
          variants={variants}
          animate={isOpen ? 'open' : 'closed'}
          transition={{ duration: 0.5 }}
        >
          <motion.li className="li-item">
            <a
              href="#bfs"
              className="btn-flip"
              data-back="BFS"
              data-front="BFS"
            ></a>
          </motion.li>
          <motion.li className="li-item">
            <a
              href="#predavaci"
              className="btn-flip"
              data-back="NAŠ TIM"
              data-front="NAŠ TIM"
            ></a>
          </motion.li>
          <motion.li className="li-item">
            <a
              href="#kursevi"
              className="btn-flip"
              data-back="PROGRAMI"
              data-front="PROGRAMI"
            ></a>
          </motion.li>
          <motion.li className="li-item">
            <a
              href="#blogs"
              className="btn-flip"
              data-back="BLOG"
              data-front="BLOG"
            ></a>
          </motion.li>
          <motion.li className="li-item">
            <a
              href="#contact"
              className="btn-flip"
              data-back="KONTAKT"
              data-front="KONTAKT"
            ></a>
          </motion.li>
        </motion.ul>
      </motion.div>
    </nav>
  );
}

export default MobileNav;
