import React from 'react';
import { motion } from 'framer-motion';

const bgAnimatio = {
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
  exit: { opacity: 0 },
};

const cardAnimation = {
  hidden: { scale: 0, opacity: 0 },
  visible: { scale: 1, opacity: 1 },
};

function DarkBG({ setShowModal }) {
  return (
    <motion.div
      className="backdrop"
      variants={bgAnimatio}
      initial="hidden"
      animate="visible"
      exit="hidden"
      transition={{ duration: 1 }}
    >
      <motion.div
        className="frame-predavacM"
        variants={cardAnimation}
        initial="hidden"
        animate="visible"
        transition={{ duration: 0.5, delay: 1 }}
      >
        <div className="predavacM-title">
          <div className="predavacM-img"></div>
          <div className="predavacM-info">
            <p>Mirjana Matić</p>
            <p>Founder &amp; Director BFS Company</p>
            <div className="icons">
              <a
                href="https://www.linkedin.com/in/mirjana-matic-99938774/"
                target="_blank"
              >
                <i className="fa-brands fa-linkedin fa-xl"></i>
              </a>
              <a href="https://www.instagram.com/mira_matic_/" target="_blank">
                <i className="fa-brands fa-instagram fa-xl"></i>
              </a>
            </div>
          </div>
          <i
            className="fa-solid fa-circle-xmark fa-2xl close-btn"
            onClick={() => {
              setShowModal(false);
            }}
          ></i>
        </div>
        <div className="predavacM-bio">
          <p>
            Svoje iskustvo sticala je u korporativnom svetu radeći dugi niz
            godina u nekoliko renomiranih kompanija, kao što su Mercator-S doo,
            Fructus doo, Tehnomanija doo, na rukovodecim pozicijama u okviru
            veleprodaje i prodaje, a posle finansija i finansijskog kontrolinga.
            Kao master ekonomista posebno se istakla kao expert u likvidnosti,
            kontroli finansijskih rezultata i kriznom menadžmentu. Danas kaže da
            upravljanje krizom ne počinje kada kriza nastana, već mnogo pre toga
            kada se vrši prevencija (zdravlje kompanije slično je zdravlju
            čoveka). Radeći u oblasti finansijskog kontrolinga stekla je
            iskustvo u vodjenju velikih projekata, pripremi integracija,
            budžeta, donošenju korektivnih akcija vezano za poslovanje, kao i
            pisanju internih procedura.
          </p>
          <p>
            Nakon korporativnog iskustva, svoju karijeru usmerila je u stvaranje
            sopstvene biznis priče kroz pružanje konsalting usluga i edukacija.
            Tokom radne karijere u kompaniji Mercator-S pružala je saradnju u
            realizaciji edukacijskih programa za zaposlene na iDEA Akademiji, a
            takodje pohadjala je i veliki broj seminara i kurseva, kao i
            Akademiju za controlling prema programu Controller Akademie,
            Gauting, Nemačka, zatim SAM Akademiju prema programu Srpske
            asocijacije menadžera, čiji je dugogodišnji član.
          </p>
          <p>
            Pored ljubavi prema finansijama, okrenuta je umetnosti i pisanju, do
            sada ima objavljene dve knjige. Prva knjiga je zbirka poezije „Ćutim
            godinama“ izdata 2010, a druga zbirka eseja i kratkih priča „Kad
            upoznah sebe“ izdata 2018. Bila je u statusu saradnika u okviru
            izdavačke kuće Književne omladine Srbije gde je povremeno radila
            recenzije novih književnih dela. Takodje dobitnik je mnogih
            zahvalnica, kao što je zahvalnica kluba „Ivo Andrić“ Zemun za
            podršku i učešće na književnoj manifestaciji „Blagodarje reči“.
          </p>
          <p>
            Baveći se naučnim radom, posebno je istraživala teme kriznog
            menadžmenta i emocionalne inteligencije, a do sada ima objavljena
            dva naučno-istraživačka rada u tom domenu, od čega je jedan
            objavljen u okviru internacionalne konferencije LIMEN 2020:
            &quot;The importance of emotional intelligence of leaders in crisis
            situations”. Preduzetništvo je takodje tema kojom se bavi već
            godinama, a u okviru Zbornika radova Nova naučna edukativna misao
            1/2014 objavila je naučni rad na temu „Inovativna rešenja za problem
            nedovoljne motivacije za preduzetništvo kod mladih“.
          </p>

          <p>
            Prvu edukaciju van korporativnog sveta držala je u Aranđelovcu, u
            saradnji sa Akademijom strukovnih studija Šumadija, na temu “Davanje
            konstruktivne povratne informacije (feedback) u timu: značaj i
            tehnike”, i upravo uspeh ove edukacije je bio prekretnica u
            postavljanju strategije sopstvenog biznisa. Danas radi na pružanju
            konsalting usluga i edukacija kako fizičkim licima, tako i
            kompanijama, a posebno voli da istakne male preduzetnike kao svoje
            klijente.
          </p>

          <p className="citat">
            „Vek u kojem živimo doneo je potpun pristup teorijskom, a i
            praktičnom znanju, ali zato nikada nije bilo potrebnije staviti
            fokus na znanje sa svrhom.“
          </p>
        </div>
      </motion.div>
    </motion.div>
  );
}

export default DarkBG;
