import React from 'react';
import logo from '../assets/logo_bfs_co.png';
import { useRef, useEffect, useState } from 'react';
import {
  motion,
  useInView,
  useAnimation,
  AnimatePresence,
} from 'framer-motion';
import { useFormik } from 'formik';
import emailjs from '@emailjs/browser';

const initialValues = {
  name: '',
  email: '',
  message: '',
};

const validate = (values) => {
  const errors = {};

  if (!values.name) {
    errors.name = 'Required';
  }
  if (!values.email) {
    errors.email = 'Required';
  } else if (
    !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
      values.email
    )
  ) {
    errors.email = 'Invalid email format';
  }

  if (!values.message) {
    errors.message = 'Required';
  }

  return errors;
};

function Contact() {
  const ref = useRef(null);
  const form = useRef();
  const isInView = useInView(ref);
  const animationControls = useAnimation();
  const [showSuccess, setShowSuccess] = useState(false);
  useEffect(() => {
    if (isInView) {
      animationControls.start('visible');
    }
  }, [isInView]);

  const onSubmit = (values, { resetForm }) => {
    console.log('Form data ', values);

    emailjs
      .sendForm(
        'service_bgpx1pa',
        'template_u94e629',
        form.current,
        '1Utt4jEgsywyNLfZv'
      )
      .then(
        (result) => {
          console.log(result.text);
          console.log('Message sent successfully!');
        },
        (error) => {
          console.log(error.text);
        }
      );

    resetForm();
    setShowSuccess(true);
    setTimeout(() => {
      setShowSuccess(false);
    }, 1000);
  };

  const formik = useFormik({
    initialValues,
    onSubmit,
    validate,
  });

  // console.log('Form errors', formik.errors);
  // console.log(formik.errors.message, formik.errors.email, formik.errors.name);
  // console.log('Visited fields', formik.touched);

  return (
    <div className="contact" id="contact">
      <motion.div
        className="frame"
        variants={{
          hidden: { y: 150, opacity: 0 },
          visible: { y: 0, opacity: 1 },
        }}
        initial="hidden"
        animate={animationControls}
        transition={{
          duration: 1,
          delay: 0.5,
        }}
      >
        {/* <div className="contact-image"></div> */}
        <div className="form">
          <h3 ref={ref}>Kontaktirajte nas</h3>
          <form onSubmit={formik.handleSubmit} ref={form}>
            <div className="form-control">
              <label htmlFor="username">Ime i prezime:</label>
              <div className="input-control">
                <input
                  type="text"
                  id="name"
                  name="name"
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.name}
                />
                <AnimatePresence>
                  {formik.touched.name && formik.errors.name ? (
                    <div>
                      <div className="error">
                        <motion.i
                          className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                          style={{ color: 'red' }}
                          initial={{ opacity: 0, scale: 0 }}
                          animate={{ opacity: 1, scale: 1 }}
                          exit={{ opacity: 0, scale: 0 }}
                        ></motion.i>
                      </div>
                    </div>
                  ) : null}
                </AnimatePresence>
              </div>
            </div>
            <div className="form-control">
              <label htmlFor="email">E-mail:</label>
              <div className="input-control">
                <input
                  type="email"
                  id="email"
                  name="email"
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.email}
                />
                <AnimatePresence>
                  {formik.touched.email && formik.errors.email ? (
                    <div className="error">
                      <motion.i
                        className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                        style={{ color: 'red' }}
                        initial={{ opacity: 0, scale: 0 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0 }}
                      ></motion.i>
                    </div>
                  ) : null}
                </AnimatePresence>
              </div>
            </div>
            <div className="form-control">
              <label htmlFor="message">Poruka:</label>
              <div className="input-control">
                <textarea
                  name="message"
                  id="message"
                  cols="30"
                  rows="10"
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.message}
                ></textarea>
                <AnimatePresence>
                  {formik.touched.message && formik.errors.message ? (
                    <div className="error-message">
                      <motion.i
                        className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                        style={{ color: 'red' }}
                        initial={{ opacity: 0, scale: 0 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0 }}
                      ></motion.i>
                    </div>
                  ) : null}
                </AnimatePresence>
              </div>
            </div>
            <button className="btn-send" type="submit">
              Pošalji
              <i className="fa-solid fa-paper-plane"></i>
            </button>
            <AnimatePresence>
              {showSuccess && (
                <motion.div
                  className="success"
                  initial={{ opacity: 0, scale: 0 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 0 }}
                  transition={{
                    duration: 0.5,
                    exit: { delay: 0.5 },
                  }}
                >
                  Uspesno ste poslali poruku
                </motion.div>
              )}
            </AnimatePresence>
          </form>
        </div>
        <div className="company-info">
          <img src={logo} alt="Logo of the BFS Compay" />
          <h3>BFS Company</h3>
          <p>
            <i class="fa-solid fa-envelope fa-sm"></i> office@bfscompany.rs
          </p>
          <p>
            <i class="fa-solid fa-location-dot fa-sm"></i> Jurija Gagarina 14nj,
            Beograd
          </p>

          <p>
            <i class="fa-solid fa-phone fa-sm"></i> +381604557700
          </p>
          <div className="icons">
            <a
              href="https://www.linkedin.com/company/bfs-institut/"
              target="_blank"
            >
              <i className="fa-brands fa-linkedin fa-xl"></i>
            </a>
            <a
              href="https://instagram.com/bfs_company?igshid=MzNlNGNkZWQ4Mg=="
              target="_blank"
            >
              <i className="fa-brands fa-instagram fa-xl"></i>
            </a>
          </div>
        </div>
      </motion.div>
    </div>
  );
}

export default Contact;
