import React from 'react';
import { useRef, useEffect, useState } from 'react';
import {
  motion,
  useInView,
  useAnimation,
  AnimatePresence,
} from 'framer-motion';
import TemplateBlogs from './TemplateBlogs';

function Blogs() {
  const ref = useRef(null);
  const imagesWidth = useRef();
  const isInView = useInView(ref);
  const animationControls = useAnimation();
  const [width, setWidth] = useState(0);
  const [x, setX] = useState(0);
  const [showBlogTemplate, setShowBlogTemplate] = useState(false);
  const [blogID, setBlogID] = useState(null);

  useEffect(() => {
    if (isInView) {
      animationControls.start('visible');
    }
  }, [isInView]);

  useEffect(() => {
    setWidth(imagesWidth.current.scrollWidth - imagesWidth.current.offsetWidth);
  }, []);

  showBlogTemplate
    ? (document.body.style.overflow = 'hidden')
    : (document.body.style.overflow = 'unset');

  return (
    <>
      <div className="section-name" id="blogs">
        <h1>BLOG</h1>
      </div>
      <div className="blogs" ref={ref}>
        <motion.div
          onClick={() => {
            if (x < 0) {
              setX(x + 380);
            }
          }}
          className="left-arrow"
          variants={{
            hidden: { scale: 0, opacity: 0 },
            visible: { scale: 1, opacity: 0.7 },
          }}
          initial="hidden"
          animate={animationControls}
          transition={{
            scale: { delay: 0.5, duration: 0.5 },
            opacity: { delay: 0.5, duration: 0.5 },
          }}
        >
          <motion.i
            className="fa-solid fa-circle-chevron-left fa-3x"
            whileHover={{ scale: 1.2 }}
            transition={{
              type: 'spring',
              stiffness: 400,
              damping: 10,
            }}
          ></motion.i>
        </motion.div>
        <AnimatePresence>
          {showBlogTemplate && (
            <TemplateBlogs
              blogID={blogID}
              setBlogID={setBlogID}
              setShowBlogTemplate={setShowBlogTemplate}
            />
          )}
        </AnimatePresence>
        <div className="slider">
          <motion.div
            ref={imagesWidth}
            className="blogs-frame"
            drag="x"
            dragConstraints={{ right: 0, left: -width }}
            animate={{ x }}
            transition={{ type: 'spring' }}
          >
            <motion.div
              id="1"
              className="blog-frame one"
              variants={{
                hidden: { y: 150, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={animationControls}
              whileHover={{ scale: 1.05 }}
              transition={{
                y: { duration: 1, delay: 1.2 },
                opacity: { duration: 1, delay: 1.2 },
                type: 'spring',
                stiffness: 400,
                damping: 10,
              }}
              onClick={(e) => {
                setBlogID(e.currentTarget.id);
                setShowBlogTemplate(true);
              }}
            >
              <p>Kako izazvati sebe na izvrsnost?</p>
            </motion.div>
            <motion.div
              id="2"
              className="blog-frame two"
              variants={{
                hidden: { y: 150, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={animationControls}
              whileHover={{ scale: 1.05 }}
              transition={{
                y: { duration: 1, delay: 1.2 },
                opacity: { duration: 1, delay: 1.2 },
                type: 'spring',
                stiffness: 400,
                damping: 10,
              }}
              onClick={(e) => {
                setBlogID(e.currentTarget.id);
                setShowBlogTemplate(true);
              }}
            >
              <p>Direktor pre tridesete?</p>
            </motion.div>
            <motion.div
              id="3"
              className="blog-frame three"
              variants={{
                hidden: { y: 150, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={animationControls}
              whileHover={{ scale: 1.05 }}
              transition={{
                y: { duration: 1, delay: 1.2 },
                opacity: { duration: 1, delay: 1.2 },
                type: 'spring',
                stiffness: 400,
                damping: 10,
              }}
              onClick={(e) => {
                setBlogID(e.currentTarget.id);
                setShowBlogTemplate(true);
              }}
            >
              <p>
                Emocionalna inteligencija i krizne <br />
                situacije
              </p>
            </motion.div>
            <motion.div
              id="4"
              className="blog-frame four"
              variants={{
                hidden: { y: 150, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={animationControls}
              whileHover={{ scale: 1.05 }}
              transition={{
                y: { duration: 1, delay: 1.2 },
                opacity: { duration: 1, delay: 1.2 },
                type: 'spring',
                stiffness: 400,
                damping: 10,
              }}
              onClick={(e) => {
                setBlogID(e.currentTarget.id);
                setShowBlogTemplate(true);
              }}
            >
              <p>
                Koje veštine treba da razvije <br /> uspešan tim
              </p>
            </motion.div>
          </motion.div>
        </div>

        <motion.div
          onClick={() => {
            if (x > -width) {
              setX(x - 380);
            }
          }}
          className="right-arrow"
          variants={{
            hidden: { scale: 0, opacity: 0 },
            visible: { scale: 1, opacity: 0.7 },
          }}
          initial="hidden"
          animate={animationControls}
          transition={{
            duration: 0.5,
            delay: 0.5,
          }}
        >
          <motion.i
            className="fa-solid fa-circle-chevron-right fa-3x"
            whileHover={{ scale: 1.2 }}
            transition={{
              type: 'spring',
              stiffness: 400,
              damping: 10,
            }}
          ></motion.i>
        </motion.div>
      </div>
    </>
  );
}

export default Blogs;
