import React from 'react';
import Header from './Header';
import { motion } from 'framer-motion';

function HeroSection() {
  return (
    <motion.div
      className="container2"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ delay: 1, duration: 0.5 }}
    >
      <Header />
      <main>
        <div className="left-animation">
          <motion.h1
            initial={{ x: -500, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{
              default: { delay: 1.5, duration: 1.5, type: 'spring' },
              opacity: { delay: 2 },
            }}
          >
            Stekni znanje
          </motion.h1>
          <motion.h1
            initial={{ x: 1500, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{
              default: { delay: 1.5, duration: 2, type: 'spring' },
              opacity: { delay: 2 },
            }}
          >
            koje ima svrhu
          </motion.h1>
          <motion.p
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 3, duration: 1.5 }}
          >
            Znanje koje će ti doneti unapređenje na poslu. <br /> Znanje koje će
            ti doneti rast sopstvenog biznisa. <br /> Znanje koje menja
            organizacije i čini ih proaktivnijim i profitabilnijim.
          </motion.p>

          <motion.button
            className="button2"
            initial={{ x: -500, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            whileHover={{ scale: 1.1 }}
            transition={{
              x: {
                delay: 3,
                duration: 1,
                type: 'spring',
                stiffness: 150,
              },
              opacity: { delay: 3, duration: 1 },
              type: 'spring',
              stiffness: 400,
              damping: 10,
            }}
          >
            <a href="#kursevi">Saznaj više</a>
          </motion.button>
        </div>
      </main>
    </motion.div>
  );
}

export default HeroSection;
