import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogTwo() {
  return (
    <div className="blog">
      <h3>Direktor/ka pre 30-te: trend ili eksperiment 21.veka?</h3>
      <p className="first-p">
        Šta biste Vi rekli na ovo? Verujem da imate u svom okruženju bar jednu
        osobu koja je uspela da se pre 30te godine popne na neku od visokih
        menadžerskih pozicija. Pre samo 30 godina tako nešto bilo je teško i
        zamisliti. Na rukovodećim pozicijama bili su isključivo ljudi sa velikim
        iskustvom, a uz to iskustvo išlo je i znanje. Kao što znamo 21.vek i
        savremeno doba je doba informacija, pa se često ispred iskustva i
        znanja, stavljaju brzina kojim se dolazi do infomacija kao i umeće rada
        sa tehnologijama. Takodje, brzina kojom se dešavaju promene u svetu
        dovele su do toga da poslodavci traže izuzetno energične ljude da vode
        biznis sektore, a „smirenost“ takodje dolazi u drugi plan, što daje
        prednost mladjim osobama u odnosu na starije i iskusnije.
      </p>
      <p className="second-p">
        Sa jedne strane, ovaj eksperiment pokazao se izuzetno u mnogim
        kompanijama širom sveta, jer su mladi ljudi pokazivali veliku motivaciju
        u radu na tim pozicijama, kao i veću inicijativu i kreativnost, ali
        takodje i dosta više grešaka u radu. Dolazimo do onoga što je
        najznačajnije, veliki procenat grešaka utiče na samopouzdanje, pa su
        upravo ovi zaposleni češće bivali u burnout sindrom-u. Poznato je da
        menadžerske pozicije podrazumevaju u najvećoj meri bavljenje ljudima.
        Savremena istraživanja pokazuju da emocionalna inteligencija utiče na
        sposobnost vodjenja tima, a da koeficijent emocionalne inteligencije
        raste godinama života i radnog iskustva. Dolazimo do zaključka, da je
        priprodan tok stvari da stariji menadžeri bolje vode timove jer imaju
        veći stepen emocionalne inteligencije.
      </p>
      <p>
        Složićete se da nije lako dati odgovor na postavljeno pitanje, ali
        pokazalo se da najbolje rezultate su ostvarivali mladji menadžeri na
        višim pozicijama uz mentorstvo starijih menadžera. Dakle, bez timskog
        rada teško je zamisliti da jedna organizacija može napredovati, pa bismo
        mogli reći da je i u ovom slučaju to dobitna kombinacija.
      </p>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Matić</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogTwo;
