import SvgAnimation from './component/SvgAnimation';

import { useState } from 'react';
import { AnimatePresence } from 'framer-motion';
import HeroSection from './component/HeroSection';
import Bfs from './component/Bfs';
import Predavaci from './component/Predavaci';
import Kursevi from './component/Kursevi';
import Blogs from './component/Blogs';
import Contact from './component/Contact';
import Footer from './component/Footer';

function App() {
  const [logoAnimation, setLogoAnimatio] = useState(true);
  const [webSite, setWebSite] = useState(false);

  setTimeout(() => {
    setLogoAnimatio(false);
    setWebSite(true);
  }, 4000);

  return (
    <div>
      <AnimatePresence>{logoAnimation && <SvgAnimation />}</AnimatePresence>
      {webSite && (
        <>
          <HeroSection />
          <Bfs />
          <Predavaci />
          <Kursevi />
          <Blogs />
          <Contact />
          <Footer />
        </>
      )}
    </div>
  );
}

export default App;
