import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import BlogOne from './blogs/BlogOne';
import BlogTwo from './blogs/BlogTwo';
import BlogThree from './blogs/BlogThree';
import BlogFour from './blogs/BlogFour';

const bgAnimatio = {
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
  exit: { opacity: 0 },
};

const cardAnimation = {
  hidden: { scale: 0, opacity: 0 },
  visible: { scale: 1, opacity: 1 },
};

function TemplateBlogs({ blogID, setShowBlogTemplate, setBlogID }) {
  const blog = () => {
    switch (blogID) {
      case '1':
        return <BlogOne />;
      case '2':
        return <BlogTwo />;
      case '3':
        return <BlogThree />;
      case '4':
        return <BlogFour />;
      default:
        return null;
    }
  };

  return (
    <motion.div
      className="backdrop"
      variants={bgAnimatio}
      initial="hidden"
      animate="visible"
      exit="hidden"
      transition={{ duration: 0.75 }}
    >
      <motion.div
        className="blog-template"
        variants={cardAnimation}
        initial="hidden"
        animate="visible"
        transition={{ duration: 0.5, delay: 1 }}
      >
        <i
          className="fa-solid fa-circle-xmark fa-2xl close"
          onClick={() => {
            setShowBlogTemplate(false);
            setBlogID(null);
          }}
        ></i>
        {blog()}
      </motion.div>
    </motion.div>
  );
}

export default TemplateBlogs;
