import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogFour() {
  return (
    <div className="blog blog-four">
      <h3>Koje veštine treba da razvije uspešan tim</h3>
      <p className="first-p">
        Pored individualnih veština, postoje veštine koje se razvijaju timski, a
        koje su ključne za novi pojam, a to je razvoj emocionalno inteligentnih
        timova.
      </p>
      <ol>
        <li>
          <p>Timska samoprocena</p>
          <p>
            Izuzetno je značajno za svakog lidera da odredi vreme za ispitivanje
            efikasnosti tima uključivajući tim u ove aktivnosti. Kreiranje
            merljivih zadataka, a zatim njihovo obradjivanje i merenje, treba
            obavljati transparentno i uz timsko razumevanje oba procesa.
            Prenošenje i komuniciranje osećanja izmedju članova tima kreće u
            osnovi od otvorene komunikacije lidera i njegove sposobnosti da
            iskaže osećanja vezana za tim i situacije kroz koje tim prolazi.
            Ključno je dozvoliti članovima tima da daju mišljenje i predloge na
            procese koje smatraju neefikasnim ili nedovoljno efikasnim.
          </p>
        </li>
        <li>
          <p>Feedback</p>
          <p>
            Značaj feedback-a ogleda se prvenstveno u jasnoj proceni, otvorenoj
            komunikaciji i poverenju izmedju članova tima.
          </p>
        </li>
        <li>
          <p>Stvaranje resursa za rad s emocijom</p>
          <p>
            Potrebno je otvoreno razgovarati o teškim problemima i bavite se
            emocijama koje okružuju tim, kao i pronaći kreativne načine za
            izražavanje emocija u grupi.
          </p>
        </li>
        <li>
          <p>Rešavanje problema proaktivno</p>
          <p>
            Emotivno inteligentni timovi mogu predvideti probleme. Za taj
            segment značajno je preuzimanje inicijative kako bi tim razumeo
            situaciju i bio efikasniji. Promene su neminovne, potrebne,
            očekivane, ali one mogu voditi u dva pravca: rast ili krizu. Jasno
            je da dobre odluke vode ka rastu kompanije, a da lose odluke vode ka
            krizi.
          </p>
        </li>
        <li>
          <p>Stvaranje afirmativnog okruženja</p>
          <p>
            Poslednja tačka ali od velikog značaja jeste stvaranje afirmativnog
            okruženja. Ovde optimizam lidera dolazi do izražaja. Bitno je da
            lider podstiče važnu i pozitivnu misiju grupe i da se usredsredi na
            ono što može da kontroliše. Pravo lideristvo se bazira na ideji da
            je potrebno da lideri emotivno dopru do svojih sledbenika i motivišu
            ih putem poistovećivanja zajedničkih vrednosti i više svrhe (Bass et
            al. 1987).
          </p>
        </li>
      </ol>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Matić</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogFour;
