import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useFormik } from 'formik';
import emailjs from '@emailjs/browser';

const bgAnimatio = {
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
  exit: { opacity: 0 },
};

const cardAnimation = {
  hidden: { scale: 0, opacity: 0 },
  visible: { scale: 1, opacity: 1 },
};

// formik validation
const initialValues = {
  name: '',
  email: '',
  phone: '',
};
const validate = (values) => {
  const errors = {};

  if (!values.name) {
    errors.name = 'Required';
  }
  if (!values.email) {
    errors.email = 'Required';
  } else if (
    !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
      values.email
    )
  ) {
    errors.email = 'Invalid email format';
  }
  if (!values.phone) {
    errors.phone = 'Required';
  } else if (!/^[0-9]+$/.test(values.phone)) {
    errors.phone = 'Invalid phone number';
  }

  return errors;
};

function TemplateKurs({ kursID, setShowModal, setKursID }) {
  const [data, setData] = useState([]);
  const [stavke, setStavke] = useState([]);
  const form = useRef();
  const [showSuccess, setShowSuccess] = useState(false);
  const getData = () => {
    fetch('kursevi.json', {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        const data = myJson.filter((item) => item.id === kursID)[0];
        setData(data);
        const { stavkeKursa } = data;
        const stavke = Object.values(stavkeKursa);
        setStavke(stavke);
      });
  };

  // getData();

  useEffect(() => {
    getData();
  }, []);

  const onSubmit = (values, { resetForm }) => {
    console.log('Form data ', values);

    emailjs
      .sendForm(
        'service_bgpx1pa',
        'template_zw10zbx',
        form.current,
        '1Utt4jEgsywyNLfZv'
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );

    resetForm();
    setShowSuccess(true);
    setTimeout(() => {
      setShowSuccess(false);
    }, 1000);
  };

  const formik = useFormik({
    initialValues,
    onSubmit,
    validate,
  });

  return (
    <motion.div
      className="backdrop"
      variants={bgAnimatio}
      initial="hidden"
      animate="visible"
      exit="hidden"
      transition={{ duration: 1 }}
    >
      <motion.div
        className="frame-kurs"
        variants={cardAnimation}
        initial="hidden"
        animate="visible"
        transition={{ duration: 0.5, delay: 1 }}
      >
        <i
          className="fa-solid fa-circle-xmark fa-2xl close"
          onClick={() => {
            setShowModal(false);
            setKursID(null);
          }}
        ></i>
        <h3>{data.title}</h3>
        <div className="kurs-content">
          <div className="kurs-info">
            <ul className="kurs-data">
              <li>{data.tipPrograma}</li>
              <li>Trajanje: {data.trajanje}</li>
              <li>Lokacija: {data.lokacija}</li>
            </ul>
            <p>{data.sadrzajKursa}</p>
            <p> Program obuhvata:</p>
            <ul className="kurs-lekcije">
              {stavke.map((stavka, idx) => (
                <li key={idx}>{stavka}</li>
              ))}
            </ul>
            <div className="kurs-ostalo">
              <p>{data.ostalo}</p>
            </div>
          </div>
          <div className="kurs-forma">
            <h4>Zakažite kosultacije sa predavačem</h4>
            <form onSubmit={formik.handleSubmit} ref={form}>
              <input
                type="hidden"
                id="kursName"
                name="kursName"
                value={data.title}
              ></input>
              <div className="form-control">
                <label htmlFor="username">Ime i prezime:</label>
                <div className="input-control">
                  <input
                    type="text"
                    id="name"
                    name="name"
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    value={formik.values.name}
                  />
                  <AnimatePresence>
                    {formik.touched.name && formik.errors.name ? (
                      <div>
                        <div className="error">
                          <motion.i
                            className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                            style={{ color: 'red' }}
                            initial={{ opacity: 0, scale: 0 }}
                            animate={{ opacity: 1, scale: 1 }}
                            exit={{ opacity: 0, scale: 0 }}
                          ></motion.i>
                        </div>
                      </div>
                    ) : null}
                  </AnimatePresence>
                </div>
              </div>
              <div className="form-control">
                <label htmlFor="email">Email:</label>
                <div className="input-control">
                  <input
                    type="email"
                    id="email"
                    name="email"
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    value={formik.values.email}
                  />
                  <AnimatePresence>
                    {formik.touched.email && formik.errors.email ? (
                      <div className="error">
                        <motion.i
                          className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                          style={{ color: 'red' }}
                          initial={{ opacity: 0, scale: 0 }}
                          animate={{ opacity: 1, scale: 1 }}
                          exit={{ opacity: 0, scale: 0 }}
                        ></motion.i>
                      </div>
                    ) : null}
                  </AnimatePresence>
                </div>
              </div>
              <div className="form-control">
                <label htmlFor="phone">Telefon:</label>
                <div className="input-control">
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    value={formik.values.phone}
                  />
                  <AnimatePresence>
                    {formik.touched.phone && formik.errors.phone ? (
                      <div className="error-message">
                        <motion.i
                          className="fa-sharp fa-solid fa-circle-exclamation fa-lg"
                          style={{ color: 'red' }}
                          initial={{ opacity: 0, scale: 0 }}
                          animate={{ opacity: 1, scale: 1 }}
                          exit={{ opacity: 0, scale: 0 }}
                        ></motion.i>
                      </div>
                    ) : null}
                  </AnimatePresence>
                </div>
              </div>
              <button type="submit" className="btn-send">
                Pošalji zahtev
              </button>
              <AnimatePresence>
                {showSuccess && (
                  <motion.div
                    className="success"
                    initial={{ opacity: 0, scale: 0 }}
                    animate={{ opacity: 1, scale: 1 }}
                    exit={{ opacity: 0, scale: 0 }}
                    transition={{
                      duration: 0.5,
                      exit: { delay: 1 },
                    }}
                  >
                    Uspesno ste poslali zahtev.
                  </motion.div>
                )}
              </AnimatePresence>
            </form>
          </div>
        </div>
      </motion.div>
    </motion.div>
  );
}

export default TemplateKurs;
