import React from 'react';
import { motion } from 'framer-motion';

const navAnimation = {
  hidden: {
    y: -50,
    opacity: 0,
  },
  visible: {
    y: 0,
    opacity: 1,
  },
};

function Nav() {
  return (
    <nav>
      <ul className="nav-menu">
        <motion.li
          className="li-item"
          variants={navAnimation}
          initial="hidden"
          animate="visible"
          transition={{ delay: 1.5, type: 'spring' }}
        >
          <a
            href="#bfs"
            className="btn-flip"
            data-back="BFS"
            data-front="BFS"
          ></a>
        </motion.li>
        <motion.li
          className="li-item"
          variants={navAnimation}
          initial="hidden"
          animate="visible"
          transition={{ delay: 1.7, type: 'spring' }}
        >
          <a
            href="#predavaci"
            className="btn-flip"
            data-back="NAŠ TIM"
            data-front="NAŠ TIM"
          ></a>
        </motion.li>
        <motion.li
          className="li-item"
          variants={navAnimation}
          initial="hidden"
          animate="visible"
          transition={{ delay: 1.9, type: 'spring' }}
        >
          <a
            href="#kursevi"
            className="btn-flip"
            data-back="PROGRAMI"
            data-front="PROGRAMI"
          ></a>
        </motion.li>
        <motion.li
          className="li-item"
          variants={navAnimation}
          initial="hidden"
          animate="visible"
          transition={{ delay: 2.1, type: 'spring' }}
        >
          <a
            href="#blogs"
            className="btn-flip"
            data-back="BLOG"
            data-front="BLOG"
          ></a>
        </motion.li>
        <motion.li
          className="li-item"
          variants={navAnimation}
          initial="hidden"
          animate="visible"
          transition={{ delay: 2.3, type: 'spring' }}
        >
          <a
            href="#contact"
            className="btn-flip"
            data-back="KONTAKT"
            data-front="KONTAKT"
          ></a>
        </motion.li>
      </ul>
    </nav>
  );
}

export default Nav;
