import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogThree() {
  return (
    <div className="blog">
      <h3>Emocionalna inteligencija i krizne situacije</h3>
      <p className="first-p">
        Kada govorimo o emocionalnoj inteligenciji u biznisu, najpre moramo
        spomenuti članak autora Golemana iz 1998 godine, koji je objavljen nakon
        sprovedenih istraživanja i koji daje zaključak da se 67% svih veština
        koje su potrebne pojedincu kako bi bio uspešan u poslovnim rezultatima,
        zapravo zasniva na emocionalnoj inteligenciji. U svom istraživanju
        Goleman je podelio veštine zaposlenog na lične sposobnosti i socijalne
        sposobnosti. Prvoj grupi pripadaju sposobnosti kao što su: samosvesnost,
        upravljanje sobom i svojim reakcijama, zatim motivacija. Drugoj grupi
        pripadaju sposobnosti kao što su: empatija i socijalne veštine, odnosno
        spretnost u budjenju poželjnih reakcija kod drugih.
      </p>
      <p className="second-p">
        Paralelno, istražiivanja vezana za krizni menadžment pokazala su da u
        periodu krize svi lideri koji su uključeni u rešavanje iste, koriste
        svoje kognitivne resurse kako bi rešili krizne situacije, baveći se
        svakodnevno problemima krize, često ne uključujući bilo kakve druge
        resurse (Bargh &amp; Chartrand, 1999). Istaživanje koje su obavili Mader
        i Smith (2009) utvrdilo je tačnost hipoteze da efekat emocija lidera u
        kriznim situacijama na vrednovanje lidera ostvaruje se preko efekta
        učesnika. Ovom studijom je dokazano da prihvatanje odgovornosti ima
        veliki uticaj na članove time, odnosno da lider koji prihvata
        odgovornost u krizi je više cenjen od strane svojih zaposlenih. Takođe,
        dokazano je da lider koji daje odgovore i pokazuje emocije u kriznim
        situacijama utiče na vrednovanje sebe kao vodje od strane članova tima
        (Arsovski 2014).
      </p>
      <p>
        Jasno je da veštine potrebne za efikasno upravljanje kriznim situacijama
        vremenom dobijaju na značaju. Jedan od razloga jeste i osećaj da
        savremeno doba donosi učestale periode krize, odnosno da se danas svaka
        nepovoljna situacija može smatrati kriznom situacijom. Emocionalna
        inteligencija lidera daje širok spektar različitih socijalnih i ličnih
        veština, koje su, možemo reći u današnjem modelu poslovanja, postale
        neophodne, a čega su postale svesne i same organizacije.
      </p>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Matić</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogThree;
